import pokemonAPI from "../api/pokemonAPI"

const getPokemons = () => {
    const pokemonsArr = Array.from(Array(650))
    return pokemonsArr.map((_, index) => index + 1)
}

const getPokemonsOptions = async () => {

    const mixedPokemons = getPokemons()
    .sort(()=> Math.random() - 0.5)
    const pokemons = await getPokemonNames(mixedPokemons.splice(0,4))

    return pokemons

}

const getPokemonNames = async([a,b,c,d] = []) => {

/*     const resp = await pokemonAPI.get(`/${a}`)
    console.log(resp.data.name, resp.data.id)
    console.log(a,b,c,d); */
    const promiseArr = [
        pokemonAPI.get(`/${a}`),
        pokemonAPI.get(`/${b}`),
        pokemonAPI.get(`/${c}`),
        pokemonAPI.get(`/${d}`)
    ]

    const [ra,rb,rc,rd] =  await Promise.all(promiseArr)
    return [
        {name: ra.data.name, id: ra.data.id},
        {name: rb.data.name, id: rb.data.id},
        {name: rc.data.name, id: rc.data.id},
        {name: rd.data.name, id: rd.data.id}
    ]
}



export default getPokemonsOptions
