<template>
<h1 v-if="!pokemon">cargando...</h1>
    <div v-else>
        <h1>¿quien es ese pokémon?</h1>
        <!--HACER: IMAGEN-->
        <PokemonPicture :pokemonid="pokemon.id" :showpokemon="showPokemon"/>
  
        <!--HACER: OPCIONES-->
        <PokemonOptions :pokemons="pokemonArr"
        @selected-pokemon="checkAnswer"/>
    <div v-if="showAnswer" class="fade-in">
        <h2>{{message}}</h2>
        <button id="newGame" @click="newGame">
            nuevo juego
        </button>
    </div>
    </div>
</template>

<script>
import PokemonPicture from '@/components/PokemonPicture.vue'
import PokemonOptions from '@/components/PokemonOptions.vue'
import getPokemonOptions from '@/helpers/getPokemonOptions'

export default {
    name: 'PokemonPage',
    components: {
        PokemonPicture,
        PokemonOptions
    },
    data(){
        return {
            pokemonArr: [],
            pokemon: null,
            showPokemon: false,
            showAnswer: false,
            message: ''
        }
    },
    methods: {
        async mixPokemonArray(){
            this.pokemonArr = await getPokemonOptions()
            const rndInt = Math.floor(Math.random()*4)
            this.pokemon = this.pokemonArr[rndInt]
        },
        checkAnswer(selectedId){
            this.showPokemon=true

            if ( selectedId === this.pokemon.id ){
                this.message = `¡Correcto! el pokemon es ${this.pokemon.name}`
            } else {
                this.message = `¡Incorrecto! el pokemon es ${this.pokemon.name}`	
            }

            this.showAnswer = true
            
        },
        newGame(){
            this.showPokemon=false
            this.showAnswer=false
            this.message=''
            this.pokemon=null
            this.mixPokemonArray()
        }
    },
    mounted(){
        this.mixPokemonArray()
    }
}
</script>

<style scoped>
    #newGame{
        background-color: #ff0000;
        color: white;
        border: none;
        border-radius: 5px;
        padding: 10px;
        cursor: pointer;
        margin-top: 10px;
        position: absolute;
    }
</style>