<template>
<div class="pokemon-container">
  <img :src="imgSrc"
  class="hidden-pokemon" 
  alt="Pokémon">
  <img v-if="showpokemon" :src="imgSrc"
  class ="fade-in" 
  alt="Pokémon">

</div>

</template>

<script>
export default {
  props: {
    pokemonid: {
      type: Number,
      required: true
    },
    showpokemon: {
      type: Boolean,
      required: true,
      default: false
    }
  },
  computed: {
    imgSrc(){
      return `https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/other/dream-world/${this.pokemonid}.svg`
    }
  }

}
</script>

<style scoped>
.pokemon-container {
    height: 200px;
}
img {
  height: 200px;
  position: absolute;
  width: 100vw;
  margin-left: -50vw;
  user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  -webkit-user-drag: none;
  -webkit-user-select: none;
}
.hidden-pokemon {
    filter: brightness(0);
}

</style>